import React, { Component } from 'react';
import { NavLink } from 'react-router-dom';
import {
    CCol,
    CRow,

} from '@coreui/react'
import {  faArrowLeft, faUndoAlt } from '@fortawesome/free-solid-svg-icons';
import Subcate from "../component/Subcate";
import CateCard from '../component/CateCard'
import Vendor from "../component/Vendor"
import "../../scss/Card.scss";
import { Link } from 'react-router-dom';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { Base_URL,imgPath } from "../../CommonRoutes/Api"
import VendorDetail from '../component/VendorDetail';
import BlockUI from 'src/CommonRoutes/BlockUI/BlockUI';


class ViewAll extends Component {
    constructor(props) {
        super(props)
        this.state = {
            cate: true,
            category: [],
            isCity: "",
            cityname: [],
            cityError: "",
            subcate: false,
            subcategory: [],
            vendor: false,
            vendordetail:false,
            detail:[],
            vendorlist: [],
            nearby: false,
            shoplist: [],
            blocking:false,
            city:localStorage.getItem("city"),

        }
    }

    componentDidMount() { 
        console.log(this.state);
        this.setState({blocking:true})
        fetch(Base_URL + "allCategories").then((response) => {
            response.json().then((result) => {
                console.log(result);
                this.setState({ category: result,blocking:false })
                setTimeout(
                    () => this.setState({ position: 1,blocking:false }), 
                    3000
                  );
            })
        })

    }

    back(){
        this.props.history.push('/dashboard');     
      }

    handleSubcat(id) {
        this.setState({ cate: false, subcate: true,blocking:true})
        fetch(Base_URL + "getSubCategorybycatid/" + id).then((response) => {
            response.json().then((result) => {
                console.log("sub", result.data);
                this.setState({ subcategory: result,blocking:false })
                setTimeout(
                    () => this.setState({ position: 1,blocking:false }), 
                    3000
                  );
            })
        })

    }

    backcate(){
        this.setState({ cate: true, subcate: false })
    }

    handleVendor(id) {
        this.setState({ cate: false, subcate: false, vendor: true,blocking:true})
        fetch(Base_URL + "getvendorbysubcatid/" + id +"/"+this.state.city).then((response) => {
            response.json().then((result) => {
                console.log("vendor", result.data);
                this.setState({ vendorlist: result,blocking:false })
                setTimeout(
                    () => this.setState({ position: 1,blocking:false }), 
                    3000
                  );
            })
        })

    }


    backsub(){
        this.setState({ cate: false, subcate: true, vendor:false})
    }

     handleVdetails=(id)=> { 
        this.setState({ all: false, cate: false, subcate: false, vendor: false, vendordetail: true,blocking:true}) 
        fetch(Base_URL + "getvendorbyid/" + id).then((response) => {
            response.json().then((result) => {
                console.log("detail", result);
               this.setState({ detail: result.data,blocking:false })
               setTimeout(
                () => this.setState({ position: 1,blocking:false }), 
                3000
              );
            })
        })

    } 

    backvendor(){
        this.setState({ cate: false, subcate:false, vendor:true,vendordetail:false})
    }


    render() {
        return (
            <div >
                { this.state.cate ?
                    <div >
                        <CRow style={{ margin: "-16px" }}>
                            <CCol md="12" lg="12" xl="12" className="content-center" style={{
                                backgroundColor: "#110730",
                                marginBottom: "0.5rem"
                            }}>
                                  <Link href="#" onClick={()=>this.back()}> <FontAwesomeIcon icon={faArrowLeft} style={{marginLeft:"-4rem",color:"orange",float:"left"}}/>  </Link> 
                                <h3 style={{ color: "orange" }} >All Categories</h3>
                            </CCol>
                        </CRow>
                        <CRow style={{backgroundColor:"white",marginTop: "-0.5rem"}}>
                        {
                        
                        this.state.category.data && this.state.category.data.length > 0 
                        ?
         
                            this.state.category.data.map((item) =>
                            <CCol xs="6" sm="6" md="6">
                                <Link href="#" onClick={() => this.handleSubcat(item.id)}>
                                    <CateCard catename={item.CatEngName}
                                        catImg={imgPath+"Category_pics/"+item.CatImage}
                                    />
                                </Link>
                                </CCol>
                        
                            ):'No data Found'}
                            </CRow>
                    </div>
                    : ""}
                { this.state.subcate ?
                    <div>
                        <CRow style={{ margin: "-16px" }}>
                            <CCol md="12" lg="12" xl="12" className="content-center" style={{
                                backgroundColor: "#110730",
                                marginBottom: "0.5rem"
                            }}>
                                 <Link href="#" onClick={()=>this.backcate()}> <FontAwesomeIcon icon={faArrowLeft} style={{marginLeft:"-4rem",color:"orange",float:"left"}}/>  </Link> 
                                <h3 style={{ color: "orange" }} >All subcategories</h3>
                            </CCol>
                        </CRow>
                           <CRow  style={{backgroundColor:"white",marginTop: "-0.5rem"}}>
                        {
                            this.state.subcategory.data && this.state.subcategory.data.length > 0 
                               ?
                            this.state.subcategory.data.map((item) =>
                            <CCol xs="6" sm="6" md="6">
                                <Link href="#" onClick={() => this.handleVendor(item.id)}><Subcate subcatename={item.SubCatEngName}
                                    subcatImg={imgPath+"SubCategory_pics/"+item.SubCatImage}
                                />
                                </Link>
                                </CCol>
                    
                            ):'No data Found'}
                            </CRow>
                    </div>
                    : ""}
                { this.state.vendor ?
                    <div>
                        <CRow style={{ margin: "-16px" }}>
                            <CCol md="12" lg="12" xl="12" className="content-center" style={{
                                backgroundColor: "#110730",
                                marginBottom: "0.5rem"
                            }}>
                             <Link href="#" onClick={()=>this.backsub()}> <FontAwesomeIcon icon={faArrowLeft} style={{marginLeft:"-4rem",color:"orange",float:"left"}} />  </Link> 
                                <h3 style={{ color: "orange" }} >Our Vendors</h3>
                            </CCol>
                        </CRow>
                      
                        {
                            this.state.vendorlist.data && this.state.vendorlist.data.length > 0 
                           ?
                               (
                        
                               /*  <Link href="#" onClick={() => this.handleVdetails(item.id)}> */
                                <Vendor   data={this.state.vendorlist.data} 
                                      handleVdetails={this.handleVdetails}
                                    
                                />
                                /* </Link> */
                                
                            ):'No data Found'}
                            
                           
                    </div>
                    : ""}
                { this.state.vendordetail ?
                    <div>
                        <CRow style={{ margin: "-16px" }}>
                            <CCol md="12" lg="12" xl="12" className="content-center" style={{
                                backgroundColor: "#110730",
                                marginBottom: "0.5rem"
                            }}>
                              <Link href="#" onClick={()=>this.backvendor()}> <FontAwesomeIcon icon={faArrowLeft} style={{marginLeft:"-4rem",color:"orange",float:"left"}}/>  </Link> 
                                <h3 style={{ color: "orange" }} >{this.state.detail.DisplayName}</h3>
                            </CCol>
                        </CRow>
                       {/*  {
                            this.state.detail && this.state.detail.length > 0 ?

                            this.state.detail.map((item) =>
                            <VendorDetail name={item.DisplayName}
                                    DisplayName={item.CompanyName}
                                    state={item.State}
                                 city={item.VendorCity}
                                 address={item.Address}
                                /> */}
                             <VendorDetail name={this.state.detail.CompanyName}
                                 displayName={this.state.detail.DisplayName}   
                                 state={this.state.detail.State}
                                 city={this.state.detail.VendorCity}
                                 address={this.state.detail.Address}
                                 mobile={this.state.detail.AccountAttachedMobileNo}
                                 pincode={this.state.detail.Pincode}
                                 lat={this.state.detail.Latitude}
                                 long={this.state.detail.Longitude}
                             /> 
                            
                    </div>
                    : ""}
                    <BlockUI blocking={this.state.blocking} />
            </div>
           
        );
    }
}

export default ViewAll;