import React from 'react';
import {
    CCard,
    CCardBody,
    CCardHeader,
    CRow,
    CCol,
   
  } from '@coreui/react'
import "../../scss/Card.scss";
import C9 from "../../assets/img/c2.jpg";
import {Base_URL} from "../../CommonRoutes/Api"


function CateCard(props) {
    return (
        
      <CCard style={{marginLeft: "-0.5rem",
      marginRight: "-0.5rem",marginTop:"0.8rem"}}>
              <CCardHeader>
               <center><img src={props.catImg}  height="150px" width="80%" /></center> 
             
              </CCardHeader>
              <CCardBody>
                 <center>{props.catename}</center>
              </CCardBody>
            </CCard>
      
      
     
    );
  }
  
  export default CateCard;